import { useState } from 'react'

function App() {
  const [ counter, setCounter ] = useState(0);

  const incr = () => {
    setCounter(counter + 1)
  }

  const decr = () => {
    setCounter(counter - 1)
  }

  return (
    <>
     {counter} <br />
     <button onClick = {incr}> increment </button>
     <button onClick = {decr}> decrement </button>
    </>
  );
}

export default App;
